<template>
  <div class="main rule-group">
    <div class="main-edit">
      <div class="edit-title">
        <h6><span></span>账号基本信息</h6>
      </div>
      <div class="edit-box">
        <div class="box-info">
          <div class="info-item">
            <span class="item-left">外呼团队</span>
            <span class="item-right">
              <el-select style="width:220px;" v-model="corpid" @change="changeStaff()">
                  <template v-for="item in teamData">
                      <el-option :label="item.memo" :value="item.id"  :key="item.memo">{{item.memo}}</el-option>
                  </template>
              </el-select>
            </span>
          </div>
          <div class="info-item">
            <span class="item-left">外呼项目</span>
            <span class="item-right">
              <el-select style="width:220px;" v-model="prjid" @change='changePrjid'>
                  <template v-for="item in projData">
                      <el-option :label="item.pmemo" :value="item.id"  :key="item.id">{{item.pmemo}}</el-option>
                  </template>
              </el-select>
            </span>
          </div>
          <div class="info-item">
            <span class="item-left">数量</span>
            <span class="item-right">
              <template>
                <input style="width:220px;" type="Number" v-model="num" class="txt-common" />
              </template>
            </span>
          </div>
          <div class="info-item">
            <span class="item-left">数据归属地</span>
            <span class="item-right">
              <template>
                <el-input style="width:372px;" placeholder="点击选择" readonly v-model="city" @click.native="dialog_batch('city')"></el-input>
              </template>
            </span>
          </div>
          <div class="info-item">
            <span class="item-left">模型</span>
            <span class="item-right">
              <template>
                <!-- <el-autocomplete style="width:372px;" class="inline-input" v-model="model" trigger-on-focus :fetch-suggestions="querySearch" @select="handleSelect" placeholder="点击搜索"></el-autocomplete> -->
                <el-select style="width:372px;" v-model="model" placeholder="请选择" filterable :filter-method="dataFilter">
                  <el-option v-for="item in options" :key="item.id" :label="item.memo" :value="item.id" />
                </el-select>
              
              </template>
              
            </span>
          </div>
        </div>
      </div>
      <div class="edit-btn-list">
        <button type="button" class="btn-submit" @click="submit">提交</button>
      </div>
      <el-dialog :visible.sync="popup_show">
        <popup-box :dataList='cityId' :dataId = 'dataId' ref="getDataName"></popup-box>
        <div slot="footer" class="dialog-footer">
          <el-button @click="popup_show=false">取消</el-button>
          <el-button type="primary" @click="save">保存</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import pager from '@/components/pager'
import popupBox from '@/components/popup'
import {get_datacmemo, get_area, get_city, get_model} from '@/api/index'
import ajax from '@/config/ajax'
import Qs from 'qs'
export default {
  name: 'proportion',
  components:{popupBox},
  data () {
    return {
      search: {
        size: 10, // 每页显示条数
        page: 1 // 页码
      },
      list: {
        data: [], // 数据集
        totalPage: 0, // 总页数
        totalCount: 0, // 总条数
      },
      num: '8000',  //数量
      city: '所有' ,   //城市
      cityvalue: '',    //城市id
      cityAllVal: '',     //所有城市id
      model: '', //模型
      modelvalue: '',  //模型id
      corp: "",   //外呼团队
      corpid: "",  //外呼团队id
      prjid: '',  //项目id
      prjid_local: '',  
      teamData: [],         //外呼团队字典数组
      cityData: [],  //城市字典数组
      areaId: [],     //根据项目id获取城市id
      cityId: [],    //匹配完的数组
      modelData: [], //数据批次字典数组  
      options: [],  
      projData: [],  //外呼项目字典树组
      csvS: [],  
      dataId: "3",    //数据批次、省份、城市数据id分别
      isCorpContainAll: true,  //外呼团队是否选中全选 
      popup_show: false
    }
  },
  created () {
    this.teamData = JSON.parse(localStorage.getItem("cropid"));
    this.cityData = JSON.parse(localStorage.getItem("city"));
    this.modelData = JSON.parse(localStorage.getItem("model"));
    this.options = this.modelData;
    this.teamData = this.teamData.slice(1);
    this.corpid = this.teamData[0].id;
    this.corp = this.teamData[0].memo;
    this.model = this.modelData[0].id;
    this.getProportionList()
  },
  watch: {
    'model': {
      handler: function() {
        this.csvS = [];//这是定义好的用于存放下拉提醒框中数据的数组
          var len = this.modelData.length;//csvList是页面初始化时得到的初始全部数据
          var arr = [];
          for (var i = 0; i < len; i++) {//根据输入框中inputName的值进行模糊匹配
            if(this.model == ''){
              arr.push(this.modelData[i]);
            }else{
              if (this.modelData[i].memo.indexOf(this.model) >= 0) {
                arr.push(this.modelData[i]);//符合条件的值都放入arr中
              }
            }
            
          }
          //el-autocomplete元素要求数组内是对象，且有value属性，此处做一个格式调整
          for (var i = 0; i < arr.length; i++) {
            var obj = { value: "" , id: ''};
            obj.value = arr[i].memo;
            obj.id = arr[i].id;
            this.csvS.push(obj);
          }
      }
    }
  },
  methods: {
    // 分页
    change_data (index, size) {
      this.list.page = index;
      this.list.size = size
    },
    changeStaff(value){
      this.corp = this.teamData.filter(item => item.id === this.corpid)[0].memo;
      this.getProportionList();
    },
    changePrjid(){
      this.getArea();
    },
    dialog_batch(type){
      this.popup_show = true;
    },
    save() {
      this.popup_show = false;
      console.log(this.$refs.getDataName.complete()[0]);
      if(this.$refs.getDataName.complete()[0] != ''){
        console.log("22222");
        this.city = this.$refs.getDataName.complete()[0].join(",");
        this.cityvalue = this.$refs.getDataName.complete()[1].join(",");
      }else{
        this.city = "所有";
        this.cityvalue = this.cityAllVal;
      }
      
    },
    submit(){
      var params = {
        mid:this.model,
        cid:this.corpid,
        num: this.num,
        area: this.cityvalue,
        prjid: this.prjid
      }
      return ajax.request({
        method:'post',
        url:'provide',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
        },
        transformRequest: [function(params) {      //在请求之前对data传参进行格式转换
          params = Qs.stringify(params)
          return params
        }],
        data:params
      }).then((res)=>{
        if(res.code == '200'){
          this.$message({
            type: 'success',
            message: '提交成功'
          })
        }
      }).catch((error)=>{
        console.log(error)
      })
    },
    querySearch(queryString, cb) {
      var csvS = this.csvS;
      cb(csvS);
    },
    handleSelect(item){
      this.modelvalue = item.id;
    },
    // 获取项目id等
    getProportionList () {
      var params = {
          cmemo: this.corp
        }
        get_datacmemo(this.corp, "pmemo/cmemo").then((res) => {
          if(res.code == '200'){
            this.projData = res.data.list;
            this.prjid = this.projData[0].id;
            this.prjid_local = this.projData[0].prjid_local;
            this.getArea();
          }
        }).catch(error => {
          console.log(error);
        })
    },
    //获取地域级模型
    getArea(){
      get_area(this.prjid_local).then((res) => {
        if(res.code == '200'){
          if(res.data.area != 'ex'){
            this.areaId = res.data.area;
            this.city = "所有";
            this.cityvalue = res.data.area.join(",");
            this.cityAllVal = res.data.area.join(",")
            this.matchCity();
          }else{
            this.cityvalue = 'ex';
            this.cityAllVal = 'ex';
            get_city();
            this.cityId = JSON.parse(localStorage.getItem("city"));
          }
          if(res.data.model != ''){
            console.log(this.modelData);
            this.modelData = res.data.model;
            this.model = this.modelData[0].id;
          }else{
            get_model();
            this.modelData = JSON.parse(localStorage.getItem("model"));
            this.model = this.modelData[0].id;
          }
        }
      }).catch(error => {

      })
    },
    //匹配城市id
    matchCity(){
      this.cityId = [];
      for(var i=0;i<this.areaId.length;i++){
        var obj = new Object();
        obj.id = this.cityData.filter(item => item.id === this.areaId[i])[0].id;
        obj.memo = this.cityData.filter(item => item.id === this.areaId[i])[0].memo;
        this.cityId.push(obj);
      }
    },
    dataFilter(val){
      if(this.model){
        this.options = this.modelData.filter((item) => {
          if (!!~item.memo.indexOf(val)) {
            return true
          }
        })
      }else{
        this.options = this.modelData;
      }
    }
  }
}
</script>

<style scoped>
  @import '../../assets/css/pages/main.css';
  .rule-group{
    padding-left: 15px;
  }
  .rule-group .main-table table{
    margin-bottom: 30px;
  }
  .main-edit .edit-box{
    margin-top:30px;
  }
  .main-edit .info-item .item-left{
    width:12%;
    color:#333
  }
  .main-edit .info-item .item-right input{
    color:#333!important;
  }
  .main-edit .info-item .item-right select{
    color:#333!important;
  }
  .el-input__inner{
    height:34px!important;
    line-height:34px!important;
  }
  .el-select-dropdown__wrap ul li{
    padding-left:20px!important;
    display:block;
  }
  .main-edit .info-item{
    margin-bottom:30px;
  }
  .main-edit .edit-btn-list{
    border-top:none;
  }
  .edit-btn-list .btn-submit{
    margin-top:30px;
    margin-left:-300px;
  }
</style>



